
$color-white: #FFFFFF;
$color-black: #000000;
$color-light: #F5F5F5;
$color-dark: #2F3037;
$color-gray: #afb0ba;
$color-dark-gray: #818181;


$font-text: 'Arial', serif;

$radius: 5px;
$base-size: 16px;
$transition: all 0.8s;

@mixin flex($flex-direction, $justify-content, $align-items) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify-content;
  align-items: $align-items;
}
